window.BroccoliFieldTable = function(broccoli){

	var $ = require('jquery');
	var it79 = require('iterate79');
	var php = require('phpjs');
	var _resMgr = broccoli.resourceMgr;

	/**
	 * ãƒ—ãƒ¬ãƒ“ãƒ¥ãƒ¼ç”¨ã®ç°¡æ˜“ãªHTMLã‚’ç”Ÿæˆã™ã‚‹
	 */
	this.mkPreviewHtml = function( fieldData, mod, callback ){
		// InstanceTreeViewã§åˆ©ç”¨ã™ã‚‹
		fieldData = fieldData||{};
		var rtn = '';
		if( fieldData.output ){
			rtn += fieldData.output;
		}
		rtn = $('<table>'+rtn+'</table>');

		callback( rtn.get(0).outerHTML );
		return;
	}

	/**
	 * ãƒ‡ãƒ¼ã‚¿ã‚’æ­£è¦åŒ–ã™ã‚‹
	 */
	this.normalizeData = function( fieldData, mode ){
		var rtn = fieldData;
		if( typeof(fieldData) !== typeof({}) ){
			rtn = {
				"resKey":'',
				"output":"",
				"header_row":0,
				"header_col":0,
				"cell_renderer":'text',
				"renderer":'simplify',
			};
		}
		return rtn;
	}

	/**
	 * ã‚¨ãƒ‡ã‚£ã‚¿UIã‚’ç”Ÿæˆ (Client Side)
	 */
	this.mkEditor = function( mod, data, elm, callback ){
		var Editor = require('./editor/editor.js');
		var editor = new Editor(broccoli, this, mod, data, elm);
		editor.init( callback );
		return;
	}

	/**
	 * ãƒ‡ãƒ¼ã‚¿ã‚’è¤‡è£½ã™ã‚‹
	 */
	this.duplicateData = function( data, callback, resources ){
		data = JSON.parse( JSON.stringify( data ) );
		it79.fnc(
			data,
			[
				function(it1, data){
					_resMgr.addResource( function(newResKey){
						_resMgr.updateResource( newResKey, resources[data.resKey], function(result){
							data.resKey = newResKey;
							it1.next(data);
						} );
					} );
				} ,
				function(it1, data){
					callback(data);
					it1.next(data);
				}
			]
		);
		return;
	}

	/**
	 * ãƒ‡ãƒ¼ã‚¿ã‹ã‚‰ä½¿ç”¨ã™ã‚‹ãƒªã‚½ãƒ¼ã‚¹ã®ãƒªã‚½ãƒ¼ã‚¹IDã‚’æŠ½å‡ºã™ã‚‹ (Client Side)
	 */
	this.extractResourceId = function( data, callback ){
		callback = callback||function(){};
		resourceIdList = [];
		resourceIdList.push(data.resKey);
		callback(resourceIdList);
		return this;
	}

	/**
	 * ã‚¨ãƒ‡ã‚£ã‚¿UIã§ç·¨é›†ã—ãŸå†…å®¹ã‚’ä¿å­˜
	 */
	this.saveEditorContent = function( elm, data, mod, callback ){
		var _this = this;
		var resInfo,
			realpathSelected;
		var $dom = $(elm);
		if( typeof(data) !== typeof({}) ){
			data = {};
		}
		if( typeof(data.resKey) !== typeof('') ){
			data.resKey = '';
		}

		it79.fnc({},
			[
				function(it1){
					data.header_row = $dom.find('input[name="'+mod.name+'__header_row"]').val();
					data.header_col = $dom.find('input[name="'+mod.name+'__header_col"]').val();
					data.editor = $dom.find('input[name="'+mod.name+'__editor"]').val();
					if( data.editor == 'html' ){
						data.cell_renderer = $dom.find('input[name="'+mod.name+'__cell_renderer"]').val();
						data.renderer = $dom.find('input[name="'+mod.name+'__renderer"]').val();
					}else{
						data.cell_renderer = $dom.find('input[name="'+mod.name+'__cell_renderer"]:checked').val();
						data.renderer = $dom.find('input[name="'+mod.name+'__renderer"]:checked').val();
					}
					it1.next();
				} ,
				function(it1){
					if( data.editor == 'html' ){
						data.output = $dom.find('textarea[name="'+mod.name+'__output"]').val();
						it1.next();
						return;
					}
					it79.fnc({},
						[
							function(it2){
								_resMgr.getResource(data.resKey, function(result){
									if( result === false ){
										_resMgr.addResource(function(newResKey){
											data.resKey = newResKey;
											it2.next(data);
										});
										return;
									}
									it2.next();
								});
							} ,
							function(it2){
								_resMgr.getResource(data.resKey, function(res){
									resInfo = res;
									it2.next();
								});
								return;
							} ,
							function(it2){
								realpathSelected = $dom.find('input[type=file]').val();

								if( realpathSelected ){
									// NOTE: Excelãƒ•ã‚¡ã‚¤ãƒ«ãŒé¸æŠžã•ã‚ŒãŸå ´åˆã€
									// é¸æŠžã•ã‚ŒãŸãƒ•ã‚¡ã‚¤ãƒ«ã®æƒ…å ±ã‚’ resourceMgr ã«ç™»éŒ²ã™ã‚‹ã€‚
									resInfo.ext = $dom.find('div[data-excel-info]').attr('data-extension');
									resInfo.type = $dom.find('div[data-excel-info]').attr('data-mime-type');
									resInfo.size = $dom.find('div[data-excel-info]').attr('data-size');
									resInfo.base64 = $dom.find('div[data-excel-info]').attr('data-base64');

									resInfo.isPrivateMaterial = true;
										// NOTE: ãƒªã‚½ãƒ¼ã‚¹ãƒ•ã‚¡ã‚¤ãƒ«ã®è¨­ç½®ã¯ resourceMgr ãŒè¡Œã£ã¦ã„ã‚‹ã€‚
										// isPrivateMaterial ãŒ true ã®å ´åˆã€å…¬é–‹é ˜åŸŸã¸ã®è¨­ç½®ã¯è¡Œã‚ã‚Œãªã„ã€‚

									_resMgr.updateResource( data.resKey, resInfo, function(){
										it2.next();
									} );
									return ;
								}else{
									// NOTE: Excelãƒ•ã‚¡ã‚¤ãƒ«ãŒé¸æŠžã•ã‚Œã¦ã„ãªã„å ´åˆã€
									// éŽåŽ»ã«ç™»éŒ²æ¸ˆã¿ã® bin.xlsx ãŒå¤‰æ›´ã•ã‚Œã¦ã„ã‚‹å¯èƒ½æ€§ãŒã‚ã‚‹ã®ã§ã€
									// bin2base64 ã§JSONã‚’æ›´æ–°ã—ã¦ãŠãã€‚
									_resMgr.resetBase64FromBin( data.resKey, function(){
										it2.next();
									} );
									return ;
								}
								it2.next();
								return ;
							} ,
							// function(it2){
							// 	// NOTE:
							// 	// â˜“ã“ã“ã§ä¸€æ—¦ä¿å­˜ã—ãªã„ã¨ã€å¤ã„ãƒ‡ãƒ¼ã‚¿ã§å¤‰æ›ã—ã¦ã—ã¾ã†ã€‚
							// 	// â—‹ã“ã“ã§ä¸€æ—¦ä¿å­˜ã—ã¡ã‚ƒã†ã¨ã€addResource() ã—ãŸæ–°ã—ã„ãƒ‡ãƒ¼ã‚¿ãŒå‰Šé™¤ã•ã‚Œã¦ã—ã¾ã†ã€‚
							// 	_resMgr.save( function(){
							// 		// var res = _resMgr.getResource( data.resKey );
							// 		it2.next();
							// 	} );
							// } ,
							function(it2){
								_this.callGpi(
									{
										'api': 'excel2html',
										'data': data
									} ,
									function(output){
										data.output = output;
										if(!php.is_string(data.output)){
											data.output = '';
										}
										it2.next();
										return;
									}
								);
							} ,
							function(){
								it1.next();
							} ,
						]
					);
				} ,
				function(it1){
					callback(data);
					it1.next();
				}
			]
		);

		return;
	} // this.saveEditorContent()
};
